export default {
	name: 'UniNumberBox',
	props: {
		value: {
			type: [Number, String],
			default: 1
		},
		min: {
			type: Number,
			default: 0
		},
		max: {
			type: Number,
			default: 100
		},
		step: {
			type: Number,
			default: 1
		},
		disabled: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			inputValue: 0
		}
	},
	watch: {
		value(val) {
			this.inputValue = +val
		},
		inputValue(newVal, oldVal) {
			if (+newVal !== +oldVal) {
				this.$emit('change', newVal)
			}
		},
		// 最大库存变化
		max(newVal,oldVal){
			if (this.inputValue > newVal) {
				this.inputValue = newVal
			}
			if(this.inputValue === 0 && newVal > 0){
				this.inputValue = 1
			}
		}
	},
	created() {
		this.inputValue = +this.value
	},
	methods: {
		_calcValue(type) {
			uni.showLoading({
				title: '加载中'
			});
			this.$emit("fun");
			
			if (this.disabled) {
				return
			}
			
			const scale = this._getDecimalScale()
			let value = this.inputValue * scale
			let step = this.step * scale
			if (type === 'minus') {
				value -= step
			} else if (type === 'plus') {
				value += step
			}
			if (value < this.min || value > this.max) {
				return
			}
			uni.hideLoading();
			this.inputValue = value / scale
		},
		_getDecimalScale() {
			let scale = 1
			// 浮点型
			if (~~this.step !== this.step) {
				scale = Math.pow(10, (this.step + '').split('.')[1].length)
			}
			return scale
		},
		_onBlur(event) {
			let value = event.detail.value
			if (!value) {
				this.inputValue = 0
				return
			}
			value = +value
			if (value > this.max) {
				value = this.max
			} else if (value < this.min) {
				value = this.min
			}
			this.inputValue = value
		}
	}
}
